@import './Global.scss';


/* #region ==================== BASE (iPAD 1024 x 768 Retina... WTF?!) ==================== */

.isotopeContainer {
    // border: 2px solid #00ffff;

    font-size: 12px;
    line-height: 14px;

    width: 100vw;

    margin: 0 auto;
    padding: 0;

    position: absolute;
    top: $header-height+$navBar-height;


    /* #region -------------------- .workNavBar -------------------- */

    .workNavBar {
        // border: 2px solid #ff0000;

        // background: #ffffff;
        // background: rgba($color: #000000, $alpha: 0.25);
        @include bgGradient01();

        text-align: center;

        width: inherit;
        height: $navBar-height+10px;
        // height: $navBar-height+15px;
        // height: $navBar-height+20px;

        margin: 0;
        padding: 0;

        position: fixed;
        z-index: 99;


        /* #region - - - - - - - - - - - .activeEmplDiv - - - - - - - - - - - */

        // .activeEmplDiv {
        //     // border: 2px inset #00ffff;

        //     // @include bgGradient01();
        //     // @include bgGradient02();
        //     // @include bgGradient03();
        //     @include bgGradient04();
        //     // @include bgGradient05();
        //     // @include bgGradient06();
        //     // @include bgGradient07();
        //     // mix-blend-mode: multiply;

        //     color: #ff0000;

        //     width: inherit;
        //     height: inherit;

        //     position: absolute;
        //     // top: $header-height;
        //     top: 0;

        //     // display: none;
        // }

        /* #endregion - - - - - - - - - - - .activeEmplDiv - - - - - - - - - - - */


        /* #region - - - - - - - - - - - .workNav - - - - - - - - - - - */

        .workNav {
            // border: 2px solid #00ff00;

            font-size: 1.25em;

            // width: inherit;
            height: inherit;

            // margin: 0;
            // padding: 0;

            display: flex;
            justify-content: center;

            position: absolute;
            left: 50%;
            transform: translate(-50%, 0);

            .activeEmplDiv, .activeTypeDiv {
                // border: 2px inset #00ffff;

                // @include bgGradient01();
                // @include bgGradient02();
                // @include bgGradient03();
                @include bgGradient04();
                // @include bgGradient05();
                // @include bgGradient06();
                // @include bgGradient07();
                // mix-blend-mode: multiply;

                color: #ff0000;

                // width: inherit;
                width: 100%;
                height: inherit;

                position: absolute;
                // top: $header-height;
                top: 0;
                // right: 0;
                z-index: -1;

                // display: none;
            }

            .workNavItem, .workNavItemLogo {
                // border: 2px solid #ff0000;

                min-width: 30px;

                margin: 0;
                // padding: 0;

                cursor: pointer;
            }

            .workNavItem {
                // border: 2px solid #00ff00;

                // margin: 0;
                // padding: 0;

                padding: 15px 15px;
            }

            .workNavItemLogo {
                // border: 2px solid #0000ff;

                margin: 0;
                // padding: 0;

                padding: 0 5px;

                position: relative;

                .workNavLogo {
                    // border: 2px solid #ff00ff;

                    width: 50px;
                    height: 80%;

                    margin: 10% 0 0 0;
                }
            }
        }

        /* #endregion - - - - - - - - - - - .workNav - - - - - - - - - - - */


        /* #region - - - - - - - - - - - .toggleNav - - - - - - - - - - - */

        .toggleNav {
            // border: 2px solid #ff0000;
            background: rgba($color: #000000, $alpha: 0.5);

            width: 11.25vw;

            margin: 0;
            padding: 5px 5px;

            position: absolute;
            // top: ($navBar-height+10px)*0.125;
            top: ($navBar-height+10px)*0.1625;
            // top: ($navBar-height+10px)*0.25;
            right: 1.25vw;

            .toggleBtn {
                // border: 2px solid #00ff00;

                color: #ffffff;

                cursor: pointer;
            }
        }

        /* #endregion - - - - - - - - - - - .toggleNav - - - - - - - - - - - */
    }

    /* #endregion -------------------- .workNavBar -------------------- */


    /* #region -------------------- .galleryContainer -------------------- */

    .galleryContainer {
        // border: 2px solid #0000ff;

        text-align: left;

        margin: 60px auto 0 auto;
        padding: 0;


        /* #region - - - - - - - - - - -  .workItem - - - - - - - - - - -  */

        .workItem {
            // border: 2px solid #00ffff;
            // border: 1px inset rgba($color: #000000, $alpha: 0.125);

            // background: rgba($color: #000000, $alpha: 0.0625);

            width: calc(16.6666666666667vw - 20px);

            // margin: 0;
            padding: 0;

            margin: 0 5px 20px 15px;

            cursor: pointer;

            img {
                width: 100%;

                // margin: 0;
                // padding: 0;
            }

            .itemInfo {
                // border: 2px solid #0000ff;
                // border: 1px inset rgba($color: #000000, $alpha: 0.125);

                // background: rgba($color: #000000, $alpha: 0.0625);
                background: linear-gradient(to top, rgba(0, 0, 0, 0.0625) 0%, rgba(255, 255, 255, 0) 80%);

                margin: 0;
                // padding: 0;
                padding: 0 0 5px 10px;

                // h3, p {
                //     font-size: 1em;
                //     // line-height: 1em;
                //     // text-align: center;
                //     // text-align: left;
                //     // vertical-align: text-bottom;

                //     // margin: 0;
                //     // padding: 0;
                //     // padding: 0 20px;
                //     // display: table-cell;
                // }

                h3 {
                    font-size: 0.9375em;

                    padding: 5px 0 0 0;
                }

                p {
                    font-size: 0.8375em;
                }
            }
        }

        /* #endregion - - - - - - - - - - -  .workItem - - - - - - - - - - -  */


        /* #region - - - - - - - - - - -  .masonryWidth2 .masonryHeight2 - - - - - - - - - - -  */

        .masonryWidth2 {
            // width: 40vw;
            // width: calc(40vw - 30px);
            // width: 33.3333333333333vw;
            // width: calc(33.3333333333333vw - 20px);
            width: calc(33.3333333333333vw - 25px);
            // width: calc(32vw - 20px);
            // width: calc(50vw - 30px);
        }

        .masonryHeight2 {
            // height: 200px;
            // height: 20vh;
            // height: calc(16.6666666666667vw * 1.5);
            height: calc(16.6666666666667vw * 2);
        }

        /* #endregion - - - - - - - - - - -  .masonryWidth2 .masonryHeight2 - - - - - - - - - - -  */
    }

    /* #endregion -------------------- .galleryContainer -------------------- */
}

/* #endregion ==================== BASE (iPAD 1024 x 768 Retina... WTF?!) ==================== */



/* #region ========================= MOBILE: 414px ========================= */

@media  
    // only screen and (min-device-width: 375px) 
    // only screen and (min-device-width: 414px)
    // only screen and (min-device-width: 375px), 
    // only screen and (min-device-width: 414px), 
    only screen and (max-device-width: 414px) 
    { 
        .isotopeContainer {
            // border: 2px solid #ff00ff;

            // margin: 0;
            // padding: 0;

            // top: 0;
            top: 50px;

            /* #region -------------------- .workNavBar -------------------- */

            .workNavBar {
                // border: 2px solid #ff0000;

                width: 20vw;
                height: auto;

                margin: 0;
                padding: 20px 0;

                top: 120px;

                // .activeEmplDiv {
                //     // border: 2px solid #00ff00;

                //     height: 100%;

                //     // visibility: hidden;
                // }

                .activeEmplDiv {
                    // border: 2px inset #00ffff;
                    left: 0;

                    display: initial;
                }

                .workNav {
                    // border: 2px solid #0000ff;

                    width: 100%;

                    // margin: 0;
                    // padding: 0;

                    flex-direction: column;
                    justify-content: flex-start;

                    top: 40px;

                    .workNavItem, .workNavItemLogo {
                        // border: 2px solid #ff0000;

                        max-height: 60px;
                        margin: 0;
                    }

                    .workNavItemLogo {
                        // border: 2px solid #00ffff;

                        // margin: 0;
                        // padding: 0;
                        // padding: 7.5px 0 2.5px 0;
                        padding: 8.75px 0 5px 0;

                        .workNavLogo {
                            // border: 2px solid #ff00ff;

                            // width: 100%;
                            width: 80%;
                            // max-width: 70%;
                            max-height: 40px;

                            margin: 0;
                            // padding: 0;
                        }
                    }
                }

                .toggleNav {
                    // border: 2px solid #00ffff;

                    width: 100%;
                    height: 40px;

                    margin: 0;
                    padding: 0;

                    vertical-align: middle;

                    top: 0;
                    left: 0;
                    right: 0;

                    // visibility: hidden;

                    .toggleBtn {
                        // border: 2px solid #0000ff;

                        width: inherit;

                        position: absolute;
                        top: 50%;
                        left: 50%;
                        transform: translate(-50%, -50%);
                    }
                }
            }

            /* #endregion -------------------- .workNavBar -------------------- */


            /* #region -------------------- .galleryContainer -------------------- */

            .galleryContainer {
                // border: 2px solid #0000ff;

                // width: 50%;
                width: 77.5vw;
                // width: 80vw;

                // margin: 0;
                // padding: 0;
                margin: 5px auto;

                // position: absolute;
                // top: 0;
                // left: 20vw;
                right: 5px;
                float: right;

                .workItem {
                    // border: 2px solid #00ffff;
                    // border: 1px inset rgba($color: #000000, $alpha: 0.125);

                    // background: rgba($color: #000000, $alpha: 0.0625);

                    // width: calc(16.6666666666667vw - 10px);
                    // width: calc(75vw);
                    width: inherit;
                    // height: auto;

                    margin: 0;
                    padding: 0;

                    // .itemInfo {
                    //     // border: 2px solid #00ffff;
                    //     // border: 1px inset rgba($color: #000000, $alpha: 0.125);

                    //     margin: 0;
                    //     padding: 0;

                    //     // visibility: hidden;
                    //     // display: none;
                    // }
                }
            }

            /* #endregion -------------------- .galleryContainer -------------------- */
        }
    }

/* #endregion ========================= MOBILE: 414px ========================= */



